@mixin card-layout-vert { 

	padding-left: gutter(13);
	padding-right: gutter(13);
	clear: left;
	float: none;
}
@mixin card-type-4col { 

	> .card__header { 
		@include span(first 3 of 12); 
	}	
	> .card__body { 
		@include span(last 9 of 12); 
		float: right;  
	}
	> .card__footer { 
		@include span(last 9 of 12); 
		clear: right; 
		float: right; 
	}
}

.card.relations {

	@include tiny   {  }
	@include mobile {  }
	@include narrow {  }
	@include wide   {  }
	@include wider  { 
		/*margin-bottom: $base-spacing-sml;
		margin-top: $base-spacing-sml; */
	}
	@include widest {
		padding-left: gutter(13);
		padding-right: gutter(13);
		clear: left;
		float: none; 
		/*border-top: none;*/
		/*margin-bottom: $base-spacing-sml;
		margin-top: $base-spacing-sml; */
	}
	@include full   { 
		@include span(isolate 12 at 4 of 18);
		/*border-top: none;*/
		/*margin-bottom: $base-spacing-huge;
		margin-top: $base-spacing-huge; */
	}

	> .card {

		&__header { 
			@include span(full);
			float: none; 
		}	
		&__body { 
			@include span(full);
			float: none; 
		}
		&__footer { 
			display:none;
			@include span(full);
			float: none;
		}
	
		@include tiny   {  }
		@include mobile {  }
		@include narrow {  }
		@include wide   {  }
		@include wider  {  
			&__header { 
				@include span(first 3 of 12); 
			}	
			&__body { 
				@include span(last 9 of 12); 
				float: right;  
			}
			&__footer { 
				@include span(last 9 of 12); 
				clear: right; 
				float: right; 
			}
		}
		@include widest	{ 
			&__header { 
				@include span(first 3 of 12); 
			}	
			&__body { 
				@include span(last 9 of 12); 
				float: right;  
			}
			&__footer { 
				@include span(last 9 of 12); 
				clear: right; 
				float: right; 
			}		
		}
		@include full { 
			
			&__header { 
				@include span(first 3 of 12); 
			}	
			&__body { 
				@include span(last 9 of 12); 
				float: right;  
			}
			&__footer { 
				@include span(last 9 of 12); 
				clear: right; 
				float: right; 
			}		
		}
	}
}

