

.border-bottom-hairline {
	border-bottom: 1px solid #f1f1f1;
}

.border-top,
.border--top {
	border-top: 1px solid #eee;
}
.border-bottom,
.border--bottom {
	border-bottom: 1px solid #eee;
}


