.inline-block {
	display: 			inline-block;
}

.nudge--left 	{ padding-left:  $base-horiz-nudge; }
.nudge--right 	{ padding-right: $base-horiz-nudge; }
.pad--left 		{ padding-left:  $base-horiz-pad; }
.pad--right 	{ padding-right: $base-horiz-pad; }
.step--left 	{ padding-left:  $base-horiz-step; }
.step--right 	{ padding-right: $base-horiz-step; }


.spaced 		{ margin: $base-spacing 0; }
.spaced--less 	{ margin: $base-spacing-sml 0; }
.spaced--more 	{ margin: $base-spacing-big 0; }
.spaced--most 	{ margin: $base-spacing-huge 0; }
.spaced--dbl 	{ margin: $base-spacing*2 0; }
.pspaced 		{ padding: $base-spacing 0; }
.pspaced--less 	{ padding: $base-spacing-sml 0; }
.pspaced--top 	{ padding-top: $base-spacing; }

.minh--hsp 	{ min-height: $base-spacing*.5; }
.minh--1sp 	{ min-height: $base-spacing; }
.minh--2sp 	{ min-height: $base-spacing*2; }

/*.height--row1x3 				{ height:	$base-row1-height *3; }*/

.clr-margin--top	{ margin-top: 0; }
.clr-margin--bottom	{ margin-bottom: 0; }
.clr-padding		{ padding: 0; }


