.position--bottom 	{ position: absolute; bottom: 0; }
.position--top 		{ position: absolute; top: 0; }

/*bd*/.line-clamp > p {

	display: block; /* Fallback for non-webkit */
	display: -webkit-box;
	/*max-width: 400px;*/
	/*height: $font-size*$line-height*$lines-to-show;*/ /* Fallback for non-webkit */
	/*margin: 0 auto;*/
	/*font-size: $font-size;
	line-height: $line-height;*/
	width: 100%;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;

}

/*bd*/.lc--4 > p {

	display: block; /* Fallback for non-webkit */
	display: -webkit-box;
	/*max-width: 400px;*/
	/*height: $font-size*$line-height*$lines-to-show;*/ /* Fallback for non-webkit */
	/*margin: 0 auto;*/
	/*font-size: $font-size;
	line-height: $line-height;*/
	width: 100%;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;

}

/*bd*/.lc--3 > p {

	display: block; /* Fallback for non-webkit */
	display: -webkit-box;
	/*max-width: 400px;*/
	/*height: $font-size*$line-height*$lines-to-show;*/ /* Fallback for non-webkit */
	/*margin: 0 auto;*/
	/*font-size: $font-size;
	line-height: $line-height;*/
	width: 100%;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;

}

/* atomic pointer events */
.pe--n { pointer-events: none; }
.pe--v { pointer-events: visible; }


.aft--arrow-r:after { content:"\27F6"; }
.bef--arrow-u:before { content:"\2191  "; }


/* vertical center */

.outer {
    display: table;
    position: absolute;
    height: 100%;
    width: 100%;
}

.middle {
  background: blue;
    display: table-cell;
    vertical-align: middle;
}

.inner {
  background: red;
    margin-left: auto;
    margin-right: auto; 
}

.overflow-hidden {
	overflow: hidden;
}
.overflow-visible,
.overflow-vis {
	overflow: visible !important;
}

.z--front { z-index: 1000; }
.z--fwd { z-index: 100 }
.z--bwd { z-index: -100 }
.z--back { z-index: -1000; }

/* text-align */
.ta--c { text-align: center; }
